.trailer {
  margin-right: 4.5vw;
  text-align: center;
}

.trailer > div {
  max-width: 100vw;
  height: auto !important;
}

.trailer video {
  border-radius: 10px;
  box-shadow: 0 8px 16px 0 rgb(0 0 0 / 20%);
}

iframe {
  width: 100%;
  height: 415px;
}

@media screen and ( max-width: 800px ) { 
  .trailer {
    margin: 0px;
  }

  .trailer > div {
    margin: auto;
  }
}

@media screen and ( max-width: 1190px ) { 
  .trailer > div {
    margin: auto;
  }
}
